@import url(https://fonts.googleapis.com/css2?family=Spartan);
#search{margin:-12vh 5% 25px 5%;font-size:medium;border-style:none;box-shadow:1px 1px 2px 0px #7b8e8e;border-radius:5px;padding:20px;position:-webkit-sticky;position:sticky;top:1%;background-color:white;display:flex;flex-wrap:wrap;padding-right:50px;grid-gap:10px;gap:10px;z-index:1}#search .button{position:absolute;right:2%;top:50%;transform:translate(0%, -50%);display:flex;grid-gap:10px;gap:10px}@media (max-width: 700px){#search .button{display:grid}}#search .clear{background:none;border-style:none;outline:none;cursor:pointer;color:#7b8e8e;font-weight:700;font-size:small}#search .clear:hover{color:#5ba4a4;text-decoration:underline}#search #location{border-left:#7b8e8e 1px solid}@media (max-width: 700px){#search #location{border-style:none}}input[type=search]:focus{outline:none}form{display:grid;grid-template-columns:60% 20% 10%;width:100%}@media (max-width: 700px){form{grid-template-columns:100%;grid-gap:20px;gap:20px}}form input[type=search]{border-style:none}form input[type=search]::-ms-clear{display:none;width:0;height:0}form input[type=search]::-ms-reveal{display:none;width:0;height:0}form input[type=search]::-webkit-search-decoration,form input[type=search]::-webkit-search-cancel-button,form input[type=search]::-webkit-search-results-button,form input[type=search]::-webkit-search-results-decoration{display:none}

body{background-color:#effafa;margin:0px}*{font-size:15px;font-family:'Spartan'}.item{margin:1% 5%;display:flex;background-color:white;box-shadow:1px 1px 5px 0px #7b8e8e;border-radius:5px;padding:0 2% 0 0;position:relative}.logo{width:100px;min-height:100%;position:relative;text-align:center}.logoImage{margin:auto 0;object-fit:contain;height:100%}.grid{display:grid;padding:0px 20px 0 0}.extraInfo{display:flex}.extraInfo p{margin-right:10px;color:#7b8e8e;font-size:small;margin-bottom:5px}.site{color:black;font-weight:700;font-family:'Spartan', sans-serif;margin:0;cursor:pointer}.site:hover{color:#5ba4a4}.tags{display:flex;flex-wrap:wrap;margin:auto 0;justify-content:flex-end;width:100%;grid-gap:2%;gap:2%;margin-right:inherit}.tags p{padding:5px;font-size:medium;color:#7b8e8e;margin-bottom:5px;font-weight:1000;color:#5ba4a4;border-radius:5px;margin:1% 0px;cursor:pointer}.tags button,.tags a{background:none;border-style:none;outline:none;cursor:pointer;color:#7b8e8e;font-weight:700;font-size:small}.tags button:hover,.tags a:hover{color:#5ba4a4;text-decoration:underline}.tags .site{color:#5ba4a4;font-weight:700;font-family:'Spartan', sans-serif;margin:0;cursor:pointer}.tags .site:hover{color:black}.jobName{color:black;font-weight:700;font-family:'Spartan', sans-serif;margin:0;cursor:pointer}.jobName:hover{color:#5ba4a4}.features{display:flex;justify-content:space-between;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;grid-gap:2%;gap:2%}.features .company{color:#5ba4a4;font-weight:700;padding:4px;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;font-weight:bolder}.new{color:white;background:#5ba4a4;border-radius:5px;border-radius:10px;padding:4px;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;font-weight:bolder}.lineBreak{display:none}@media (max-width: 700px){.item{display:block;padding:25px 20px 20px 20px;margin:50px 3%}.tags{justify-content:flex-start}.lineBreak{display:block}.logo{position:absolute;top:-50px;text-align:start;min-height:-webkit-fit-content;min-height:-moz-fit-content;min-height:fit-content}.logoImage{height:100px}.grid{padding:0px}}

a{line-height:300%}

